import Vue from 'vue'
var ink_filters = {};
var nowms = Date.now();
ink_filters.install = function(vue) {
    Vue.filter("time", function(val, isconcrete) {
        var ms = val * 1000;
        var s = nowms - ms;
        if (!isconcrete) {
            if (s < 86400000) {
                return "";
            }
            if (s < 172800000) {
                return "";
            }
            if (s < 900000) {
                return "";
            }
        }
        var dt = new Date(ms);
        return (dt.getMonth() + 1) + "" + dt.getDate() + "";
    })
}
module.exports = ink_filters;


